.Tile {
    display: inline-block;
    height: 150px;
    width: 150px;
    font-size: 40px;
    text-align: center;
    line-height: 150px;
    border-radius: 8px;
    border: 1px solid gray;
}

@media (max-width: 1100px) {

    .Tile {
        height: 100px;
        width: 100px;
        line-height: 100px;
    }
    
}